import 'bootstrap/dist/css/bootstrap.min.css';
import { useEffect, useState } from 'react';
import { Table } from '../components/Table';

export default {
  title: 'WORKOUT/Series',
  component: Table,
};

export const Template = args => {
  const [_data, setData] = useState([]);
  const [_metadata, setMetadata] = useState([]);
  useEffect(() => {
    setData([
      { id: 1, date: '2022-11-23', repetitions: 15, weight: 100 },
      { id: 2, date: '2022-11-23', repetitions: 10, weight: 18 },
    ]);
    setMetadata([
      { id: { type: 'text' } },
      { date: { name: 'date', type: 'date' } },
      { repetitions: { name: 'repetitions', type: 'number' } },
      { weigth: { name: 'weigth', type: 'number' } },
    ]);
  }, []);
  const onItemChange = item => {
    console.log(item);
  };

  return <Table onItemChange={onItemChange} data={_data} metadata={_metadata} {...args} />;
};;

export const Monday = Template.bind({});
Monday.args = {
  title: 'Curl biceps',
  muscleGroup: 'Biceps',
  max: 5,
  min: 1,
  handleOnSave: data => {
    console.log('saving');
    console.log(data);
  },
};

