import React, { useEffect } from 'react';
import { Button } from '../Button';

export const Row = ({ item, onItemChange }) => {
  return (
    <tr>
      {Object.keys(item).map(key => {
        if (key === 'id') {
          return (
            <th key={key} scope="row">
              {item[key]}
            </th>
          );
        } else {
          return (
            <td key={key}>
              {!!item.editable ? (
                <input
                  className="form-control"
                  type={item[key].type}
                  value={item[key]}
                  onChange={event => {
                    console.log(item);
                    onItemChange({
                      ...item,
                      [key]: { value: event.target, type: item[key].type },
                    });
                    console.log(item);
                  }}
                />
              ) : (
                item[key]
              )}
            </td>
          );
        }
      })}
    </tr>
  );
};
